'use client';

import { useState, useCallback, useEffect } from 'react';
import axios from '@/lib/axios';
import { Conversation, Message } from '@/types';
import { toast } from 'sonner';

export function useCustomerChat() {
    const [conversations, setConversations] = useState<Conversation[]>([]);
    const [selectedConversation, setSelectedConversation] = useState<Conversation | null>(null);
    const [messages, setMessages] = useState<Message[]>([]);
    const [loading, setLoading] = useState(false);
    const [sendingMessage, setSendingMessage] = useState(false);

    // 1. Fetch semua percakapan customer
    const fetchConversations = useCallback(async () => {
        setLoading(true);
        try {
            const response = await axios.get('/api/conversations');
            const data = response.data?.data || response.data || [];
            setConversations(data);
        } catch (error: any) {
            console.error('Error fetching conversations:', error);
            toast.error('Gagal memuat percakapan');
        } finally {
            setLoading(false);
        }
    }, []);

    // 2. Buat percakapan baru
    const createConversation = useCallback(async (subject?: string) => {
        try {
            const response = await axios.post('/api/conversations', {
                subject: subject || 'Pertanyaan Baru'
            });
            const newConversation = response.data?.data || response.data;
            setConversations(prev => [newConversation, ...prev]);
            setSelectedConversation(newConversation);
            toast.success('Percakapan baru dimulai');
            return newConversation;
        } catch (error: any) {
            const errorMsg = error.response?.data?.message || 'Gagal membuat percakapan';
            toast.error(errorMsg);
            throw error;
        }
    }, []);

    // 3. Fetch pesan dari percakapan tertentu
    const fetchMessages = useCallback(async (conversationId: number) => {
        try {
            const response = await axios.get(`/api/conversations/${conversationId}/messages`);
            const data = response.data?.data || response.data || [];
            setMessages(data);
        } catch (error: any) {
            console.error('Error fetching messages:', error);
            toast.error('Gagal memuat pesan');
        }
    }, []);

    // 4. Kirim pesan baru
    const sendMessage = useCallback(async (conversationId: number, messageText: string) => {
        if (!messageText.trim()) return;

        setSendingMessage(true);
        try {
            const response = await axios.post(`/api/conversations/${conversationId}/messages`, {
                message: messageText.trim()
            });
            const newMessage = response.data?.data || response.data;
            setMessages(prev => [...prev, newMessage]);
            
            // Update last message di conversation
            setConversations(prev => prev.map(conv =>
                conv.id === conversationId
                    ? { ...conv, last_message: messageText, last_message_at: new Date().toISOString() }
                    : conv
            ));
        } catch (error: any) {
            const errorMsg = error.response?.data?.message || 'Gagal mengirim pesan';
            toast.error(errorMsg);
            throw error;
        } finally {
            setSendingMessage(false);
        }
    }, []);

    // 5. Mark conversation as read
    const markConversationAsRead = useCallback(async (conversationId: number) => {
        try {
            await axios.patch(`/api/conversations/${conversationId}/read`);
            setConversations(prev => prev.map(conv =>
                conv.id === conversationId
                    ? { ...conv, unread_count: 0 }
                    : conv
            ));
        } catch (error) {
            console.error('Error marking as read:', error);
        }
    }, []);

    // 6. Close conversation
    const closeConversation = useCallback(async (conversationId: number) => {
        try {
            const response = await axios.patch(`/api/conversations/${conversationId}/close`);
            const updatedConversation = response.data?.data || response.data;
            setConversations(prev => prev.map(conv =>
                conv.id === conversationId ? updatedConversation : conv
            ));
            toast.success('Percakapan ditutup');
        } catch (error: any) {
            const errorMsg = error.response?.data?.message || 'Gagal menutup percakapan';
            toast.error(errorMsg);
        }
    }, []);

    // Auto fetch conversations on mount
    useEffect(() => {
        fetchConversations();
        // Polling setiap 10 detik untuk update
        const interval = setInterval(fetchConversations, 10000);
        return () => clearInterval(interval);
    }, [fetchConversations]);

    return {
        conversations,
        selectedConversation,
        setSelectedConversation,
        messages,
        loading,
        sendingMessage,
        fetchConversations,
        createConversation,
        fetchMessages,
        sendMessage,
        markConversationAsRead,
        closeConversation
    };
}
