'use client';

import { useState } from 'react';
import { Textarea } from '@/components/ui/textarea';
import { Button } from '@/components/ui/button';
import { Send, Loader2 } from 'lucide-react';

interface MessageInputProps {
    onSend: (message: string) => Promise<void>;
    disabled?: boolean;
    isLoading?: boolean;
}

export default function MessageInput({ onSend, disabled = false, isLoading = false }: MessageInputProps) {
    const [message, setMessage] = useState('');

    const handleSend = async () => {
        if (!message.trim() || disabled || isLoading) return;

        try {
            await onSend(message);
            setMessage('');
        } catch (error) {
            // Error sudah ditangani
        }
    };

    const handleKeyDown = (e: React.KeyboardEvent) => {
        if (e.key === 'Enter' && e.ctrlKey) {
            handleSend();
        }
    };

    return (
        <div className="border-t bg-white p-4 space-y-2">
            <Textarea
                placeholder="Ketik pesan Anda di sini... (Ctrl+Enter untuk mengirim)"
                value={message}
                onChange={(e) => setMessage(e.target.value)}
                onKeyDown={handleKeyDown}
                disabled={disabled || isLoading}
                rows={3}
                className="resize-none focus:ring-blue-500"
            />
            <div className="flex justify-end gap-2">
                <Button
                    onClick={handleSend}
                    disabled={!message.trim() || disabled || isLoading}
                    className="bg-blue-600 hover:bg-blue-700 text-white"
                >
                    {isLoading ? (
                        <>
                            <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                            Mengirim...
                        </>
                    ) : (
                        <>
                            <Send className="w-4 h-4 mr-2" />
                            Kirim
                        </>
                    )}
                </Button>
            </div>
        </div>
    );
}
