'use client';

import { Conversation } from '@/types';
import { formatDistanceToNow } from 'date-fns';
import { id } from 'date-fns/locale';
import { Badge } from '@/components/ui/badge';
import { X } from 'lucide-react';

interface ConversationListProps {
    conversations: Conversation[];
    selectedId?: number;
    onSelect: (conversation: Conversation) => void;
    onClose?: (id: number) => void;
}

export default function ConversationList({
    conversations,
    selectedId,
    onSelect,
    onClose
}: ConversationListProps) {
    if (conversations.length === 0) {
        return (
            <div className="h-full flex items-center justify-center text-gray-500 text-center p-4">
                <div>
                    <p className="font-medium">Belum ada percakapan</p>
                    <p className="text-sm mt-1">Mulai percakapan baru untuk menghubungi admin</p>
                </div>
            </div>
        );
    }

    return (
        <div className="space-y-2 overflow-y-auto">
            {conversations.map((conv) => (
                <div
                    key={conv.id}
                    onClick={() => onSelect(conv)}
                    className={`p-3 rounded-lg cursor-pointer transition-colors relative group ${
                        selectedId === conv.id
                            ? 'bg-blue-50 border-l-4 border-blue-600'
                            : 'bg-gray-50 hover:bg-gray-100'
                    }`}
                >
                    <div className="flex justify-between items-start gap-2">
                        <div className="flex-1 min-w-0">
                            <p className="font-semibold text-gray-900 text-sm truncate">
                                {conv.subject || 'Chat'}
                            </p>
                            <p className="text-xs text-gray-500 truncate mt-1">
                                {conv.last_message || 'Belum ada pesan'}
                            </p>
                            <p className="text-xs text-gray-400 mt-1">
                                {conv.last_message_at
                                    ? formatDistanceToNow(new Date(conv.last_message_at), { locale: id })
                                    : 'baru'}
                            </p>
                        </div>
                        {!conv.is_active && (
                            <Badge variant="secondary" className="text-xs flex-shrink-0">
                                Tertutup
                            </Badge>
                        )}
                    </div>
                </div>
            ))}
        </div>
    );
}
