'use client';

import { useEffect } from 'react';
import { useAuth } from '@/hooks/useAuth';
import { useCustomerChat } from '@/hooks/pelanggan/useCustomerChat';
import { Button } from '@/components/ui/button';
import { Plus, MessageCircle, Loader2 } from 'lucide-react';
import ConversationList from '@/components/pelanggan/chat/ConversationList';
import ChatDetail from '@/components/pelanggan/chat/ChatDetail';
import { redirect } from 'next/navigation';

export default function CustomerChatPage() {
    const { user } = useAuth();
    const {
        conversations,
        selectedConversation,
        setSelectedConversation,
        messages,
        loading,
        sendingMessage,
        fetchConversations,
        createConversation,
        fetchMessages,
        sendMessage,
        markConversationAsRead,
        closeConversation
    } = useCustomerChat();

    useEffect(() => {
        if (!user) {
            redirect('/login');
        }
    }, [user]);

    const handleSelectConversation = async (conv: any) => {
        setSelectedConversation(conv);
        await markConversationAsRead(conv.id);
        await fetchMessages(conv.id);
    };

    const handleSendMessage = async (message: string) => {
        if (!selectedConversation) return;
        await sendMessage(selectedConversation.id, message);
    };

    const handleCreateNew = async () => {
        await createConversation('Pertanyaan Baru');
    };

    const handleCloseConversation = async () => {
        if (!selectedConversation) return;
        await closeConversation(selectedConversation.id);
        setSelectedConversation(null);
    };

    return (
        <div className="h-screen flex flex-col bg-gray-50">
            {/* Header */}
            <div className="bg-white border-b p-6">
                <div className="max-w-7xl mx-auto">
                    <div className="flex items-center justify-between">
                        <div className="flex items-center gap-3">
                            <MessageCircle className="w-8 h-8 text-blue-600" />
                            <div>
                                <h1 className="text-3xl font-bold text-gray-900">Chat dengan Admin</h1>
                                <p className="text-gray-600 text-sm mt-1">Hubungi admin untuk pertanyaan atau bantuan</p>
                            </div>
                        </div>
                        <Button
                            onClick={handleCreateNew}
                            className="bg-blue-600 hover:bg-blue-700 text-white"
                        >
                            <Plus className="w-4 h-4 mr-2" />
                            Chat Baru
                        </Button>
                    </div>
                </div>
            </div>

            {/* Content */}
            <div className="flex-1 overflow-hidden flex max-w-7xl mx-auto w-full gap-4 p-4">
                {/* Conversation List */}
                <div className="w-full md:w-80 bg-white rounded-lg border flex flex-col">
                    <div className="p-4 border-b">
                        <h2 className="font-semibold text-gray-900">Percakapan</h2>
                    </div>
                    {loading && conversations.length === 0 ? (
                        <div className="flex items-center justify-center flex-1">
                            <Loader2 className="w-6 h-6 animate-spin text-blue-600" />
                        </div>
                    ) : (
                        <ConversationList
                            conversations={conversations}
                            selectedId={selectedConversation?.id}
                            onSelect={handleSelectConversation}
                        />
                    )}
                </div>

                {/* Chat Detail */}
                <div className="flex-1 hidden md:flex">
                    <ChatDetail
                        conversation={selectedConversation}
                        messages={messages}
                        loading={loading}
                        sendingMessage={sendingMessage}
                        currentUserId={user?.id || 0}
                        onSendMessage={handleSendMessage}
                        onClose={() => setSelectedConversation(null)}
                        onCloseConversation={handleCloseConversation}
                    />
                </div>
            </div>

            {/* Mobile Chat Detail */}
            {selectedConversation && (
                <div className="md:hidden fixed inset-0 bg-black/50 z-40 p-4">
                    <div className="bg-white rounded-lg h-full flex flex-col mt-16">
                        <ChatDetail
                            conversation={selectedConversation}
                            messages={messages}
                            loading={loading}
                            sendingMessage={sendingMessage}
                            currentUserId={user?.id || 0}
                            onSendMessage={handleSendMessage}
                            onClose={() => setSelectedConversation(null)}
                            onCloseConversation={handleCloseConversation}
                        />
                    </div>
                </div>
            )}
        </div>
    );
}
