'use client';

import { Conversation } from '@/types';
import { Button } from '@/components/ui/button';
import { X, Loader2 } from 'lucide-react';
import MessageList from './MessageList';
import MessageInput from './MessageInput';

interface ChatDetailProps {
    conversation: Conversation | null;
    messages: any[];
    loading: boolean;
    sendingMessage: boolean;
    currentUserId: number;
    onSendMessage: (text: string) => Promise<void>;
    onClose: () => void;
    onCloseConversation?: (id: number) => Promise<void>;
}

export default function ChatDetail({
    conversation,
    messages,
    loading,
    sendingMessage,
    currentUserId,
    onSendMessage,
    onClose,
    onCloseConversation
}: ChatDetailProps) {
    if (!conversation) {
        return (
            <div className="h-full flex items-center justify-center text-gray-500 text-center">
                <div>
                    <p className="font-medium">Pilih percakapan untuk melihat detail</p>
                </div>
            </div>
        );
    }

    return (
        <div className="flex flex-col h-full bg-white rounded-lg border">
            {/* Header */}
            <div className="flex justify-between items-center p-4 border-b bg-gradient-to-r from-blue-50 to-blue-100">
                <div>
                    <h3 className="font-semibold text-gray-900">{conversation.subject || 'Chat'}</h3>
                    <p className={`text-xs mt-1 ${conversation.is_active ? 'text-green-600' : 'text-gray-500'}`}>
                        {conversation.is_active ? '● Aktif' : '● Percakapan Ditutup'}
                    </p>
                </div>
                <Button
                    variant="ghost"
                    size="icon"
                    onClick={onClose}
                    className="text-gray-600 hover:text-gray-900"
                >
                    <X className="w-5 h-5" />
                </Button>
            </div>

            {/* Messages */}
            <MessageList messages={messages} loading={loading} currentUserId={currentUserId} />

            {/* Input */}
            {conversation.is_active ? (
                <MessageInput
                    onSend={onSendMessage}
                    disabled={false}
                    isLoading={sendingMessage}
                />
            ) : (
                <div className="p-4 text-center text-sm text-gray-500 bg-gray-50 border-t">
                    <p>Percakapan ini telah ditutup</p>
                </div>
            )}
        </div>
    );
}
